<template>
    <div class="search">
        <div class="search-title">
            <p class="sub-title">搜尋結果</p>
            <p class="title">SEARCH</p>
        </div>
        <div class="search-content">
            <div class="search-link"></div>
            <div class="buycars-search">
                <el-input placeholder="請輸入搜尋關鍵字" v-model="keyword" class="input-with-select">
                    <el-button slot="append" @click="openInfo()" icon="el-icon-search"></el-button>
                </el-input>
            </div>
            <div class="search-content-title">
                <p class="content-title">搜尋結果<span/></p>
                <el-tag type="info">Aston</el-tag>、
                <el-tag type="info">中古二手</el-tag>、
                <el-tag type="info">年份2019/11</el-tag>
            </div>
            <div class="search-result">
                <div class="search-result-card" v-for="item in 5" :key="item">
                    <div class="card-img" @click="openSearchInfo">
                        <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="">
                    </div>
                    <div class="card-info">
                        <p class="card-tag"><i class="el-icon-price-tag"/>二手車商</p>
                        <p class="card-title">Aston Martin Vantage</p>
                        <p class="card-txt">In a pickup market gone fancy, the Silverado sticks to its basic-truck recipe. The steering is accurate, and the Silverado handles more like a big car than a big truck.</p>
                        <div class="card-money">
                            <img src="~@/assets/img/cartype/money-icon-w.png" alt="" srcset=""> 
                            <p>149.8 萬</p>
                        </div>
                        <div class="card-collect">
                            <div :class="{'card-collect-active': collect }" @click="collectCar"><i class="el-icon-star-off" :class="{'el-icon-star-on': collect }"/>325 人蒐藏</div>
                            <span />
                            <div><i class="el-icon-view"/>1368 人瀏覽</div>
                        </div>
                    </div>
                </div>
                <el-pagination
                    small
                    layout="prev, pager, next"
                    :total="50">
                </el-pagination>
                
            </div>
        </div>
    </div>
    
</template>

<script>
import Cartype from '../cartype'
export default {
    components: {
        Cartype,
    },
    name:'search',
    data() {
        return {
            collect: false,
            keyword:'',
        }
    },
    methods: {
        collectCar() {
            this.collect = !this.collect
        },
        openSearchInfo(){
            this.$router.push('/searchInfo')
        },
        openInfo() {
            
        }
    }
    
}
</script>

<style lang="scss" scoped>
body {
    overflow-x: hidden;
}
.search {
    margin: 105px auto;
    .search-title {
        background: url('~@/assets/img/search/search-banner.png') no-repeat center;
        background-size: cover;
    }
    .content-title {
        padding-left:15px;
        border-left:5px solid #ef6d38;
        white-space:nowrap;    
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        span {
            margin-left: 15px;
            width: 100%;
            height: 1px;
            background: #dddddd;
            display: block;
        }
    }
    .buycars-search {
        width: 100%;
        margin: auto;
        >>>.el-input__inner {
            width: 100%;
            border-radius: 50px 0 0 50px;
        }
        >>>.el-input-group__append {
            background: #ef6d38;
            color: #fff;
            border-radius: 0 50px 50px 0;
        }
    }
    .search-content {
        max-width: 1200px;
        margin: 50px auto;
        margin-top: 100px;
        width: 90%;
        position: relative;
        .search-content-title {
            margin: 50px 0;
        }
        .search-result {
            text-align: center;
            .search-result-card{
                text-align: left;
                display: flex;
                padding: 30px 0;
                border-bottom:1px #c7c7c7 solid;
                &:nth-last-child(2) {
                    border-bottom:none
                }
                .card-img {
                    position: relative;
                    cursor: pointer;
                    img {
                        width: 300px;
                        height: 330px;
                    }
                    &::after {
                        content: " ";
                        position: absolute;
                        top: -10px;
                        left: -10px;
                        z-index: -1;
                        transition: all .3s;
                        border-style: solid;
                        border-width: 330px 300px 0 0;
                        border-color: #999999 transparent transparent transparent;
                    }
                    &:hover {
                        &::after {
                            border-color: #ef6d38 transparent transparent transparent;
                        }
                    }
                }
                .card-info {
                    margin-left: 20px;
                    
                    .card-tag {
                        color: #fff;
                        background: #3cb07b;
                        line-height: 40px;
                        border-radius: 50px;
                        text-align: center;
                        padding: 0 30px 0 45px;
                        position: relative;
                        font-weight: bold;
                        max-width: 90px;
                        i{
                            background: #148451;
                            border-radius: 50px;
                            line-height: 30px;
                            width: 30px;
                            transform: rotate(45deg);
                            position: absolute;
                            top: 5px;
                            left: 5px;
                        }
                    }
                    .card-title {
                        font-size: 22px;
                        font-weight: bold;
                        margin: 30px 0 15px 0;
                        text-decoration: underline;
                    }
                    .card-txt {
                        max-width: 400px;
                        line-height: 25px;
                    }
                    .card-money {
                        background: #ef6d38;
                        line-height: 50px;
                        color: #fff;
                        font-size: 24px;
                        display: flex;
                        margin: 30px 0;
                        max-width: 225px;
                        img {
                            background: #ca582a;
                            padding:10px;
                        }
                        p {
                            padding-left: 10px;
                        }
                    }
                    .card-collect {
                        position: absolute;
                        right: 0;
                        display: flex;
                        div {
                            margin: 10px;
                            padding: 0 10px;
                            transition: all .3s;
                            cursor: pointer;
                            i {
                                margin-right: 10px;
                            }
                            :first-child {
                                &:hover {
                                    color: #ef6d38;
                                }
                            }
                        }
                        .card-collect-active {
                            color: #ef6d38;
                        }
                        span {
                            width: 1px;
                            display: block;
                            background: #dddddd;
                        }
                    }
                }
                @media screen and (max-width: 768px){  
                    align-items: center;
                    flex-direction: column;
                    .card-info {
                        margin: 20px 0;
                        .card-title {
                            text-align: center;
                        }
                        .card-money {
                            margin: 30px auto;
                        }
                        .card-collect {
                            margin: auto;
                            right: auto;
                        }
                    }
                    .card-tag {
                        margin: auto;
                    }
                }
            }
            
        }
        
    }
    .cartype-box {
        background:#f4f4f4;
        padding:30px 0;
    }

}

</style>


